.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster{
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    transition: transform 300ms;
    margin-right: 5px;
}
.row_posterLarge{
    max-height: 250px;
}
.row_poster:hover{
    transform: scale(1.08);
    cursor: pointer;
}
.row_posterLarge:hover{
    transform: scale(1.09);
}
.row{
    color: white;
    margin-left: 10px;
}
.row_bdp{
    display: none;
    
}